<template>
  <div class="iframeContainer">
    <!--      当前的组件主要的功能是用来实现对iframe的实现-->
    <van-nav-bar
      left-text="返回"
      @click-left="$router.replace('/')"
      left-arrow
    />
    <iframe :src="url"  @load="load" id="video_frame">

    </iframe>
  </div>
</template>

<script>


  import AdBlock from '../../assets/adBlock/AdBlock'
  import AdDump from '../../assets/adDump/AdDump'


  export default {
    name: "Iframe.vue",
    data() {
      return {
        url: '',
      }
    },
    mounted() {
      this.url = this.$route.query.link;
    },


    methods: {

      load() {
        //加载完成

        if (this.url.indexOf("v.qq.com") !== -1) {
          this.processVqq();
        }

        if (this.url.indexOf("www.lezhutv.com") !== -1) {
          this.processLeZhuTv();
        }


      },

      processVqq() {


      },

      processLeZhuTv() {
        AdBlock.block(this.url);
      },


    }


  }
</script>

<style scoped>


  iframe {
    width: 100%;
    height: 94%;
    border: none;
    display: block;
    position: absolute;
  }

  >>> .van-nav-bar__text {
    color: #4fc08d;
  }

</style>



