import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import MainPage from "../components/MainPage";
import SearchPage from "../components/page/SearchPage";
import NetNav from "../components/page/NetNav";
import TestWebJs from "../components/page/TestWebJs";
import Iframe from "../components/page/Iframe";

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Page',
      component: MainPage,
      redirect: "/NetNav",
      children: [
        {
          path: '/SearchPage',
          name: 'SearchPage',
          component: SearchPage
        },
        {
          path: '/NetNav',
          name: 'NetNav',
          component: NetNav
        }, {
          path: '/TestWebJs',
          name: 'TestWebJs',
          component: TestWebJs
        }
      ]
    },
    {
      path: '/Iframe',
      name: 'Iframe',
      component: Iframe
    }
  ]
})



